ul {
    list-style: none;
}

.dark-overlay {
    display: block;
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    min-width: 100%;
    min-height: 120%;
    background-color: rgba(0, 0, 0, 0.7);
}

#hamburger-icon {
    display: none;
}

#hamburger-menu {
    display: none;
}

.main-nav {
    display: flex;
    align-items: center; 
    justify-content: center;
}

#header li {
    color: var(--dark-grayish-blue);
    margin: 0 35px 0 35px;
    float: left;
}

#header a:hover {
    color: black;
    text-decoration: underline;
    text-underline-offset: 2.5rem;
    text-decoration-thickness: 3px;
    text-decoration-color: var(--orange);
}

.floating-total {
    position: absolute;
    background-color: var(--orange);
    color: white;
    top: -15px;
    left: 10px;
    border-radius: 30px;
    padding: 0 10px 0 10px;
    font-size: 11px;
}

.push {
    /* display: flex;
    align-items: center;
    justify-content: center; */
    margin-left: 1000px;
}

.cart-div {
    position: relative;
    margin-left: 250px;
}

.shopping-cart-icon:hover {
    cursor: pointer;
    filter: brightness(0%);
}

.profile-pic {
    max-height: 45px;
    border: 2px solid transparent;
    border-radius: 100%;
}

.profile-pic:hover {
    cursor: pointer;
    border: 2px solid var(--orange);
    border-radius: 100%;
}

#main-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 65px;
}

.product-display-desktop {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.product-display-mobile {
    display: none;
}

.product-main-image {
    max-width: 33vw;
}

.product-sub-images {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 25px;
}

.product-image {
    border-radius: 15px;
    margin-top: 100px;
}

.product-image:hover {
    cursor: pointer;
}

.product-image-large {
    max-width: 25vw;
}

.product-image-small {
    width: 5.3vw;
    margin: 0 5px 0 5px;
    border: 3px solid transparent;
}

.product-image-small-bg {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    border-radius: 15px;
    width: 5.3vw;
    height: 10vh;
    margin: 0 7px 0 7px;
}

.selected-image {
    /* border: 3px solid var(--orange); */
    border: 3px solid var(--orange);
    /* background-color: rgba(255, 255, 255, 1); */
    filter: opacity(0.5);

}


.product-detail {
    max-width: 20vw;
    margin-left: 100px;
}

.product-head {
    color: var(--orange);
    font-weight: 700;
}

.product-title {
    font-weight: 1000;
}

.current-price {
    display: flex;
    align-items: center;
}

.product-sub-images {
    display: flex;
}

.discount {
    margin-left: 18px;
    color: var(--orange);
    background-color: var(--pale-orange);
    padding: 3px 10px 3px 10px;
    border-radius: 10px;
}

.old-price {
    color: var(--grayish-blue);
    text-decoration: line-through;
    margin-top: -10px;
}

.add-remove-cart {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.add-remove-amount {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 2px 2px 4px 2px;
    margin: 0 15px 0 0;
    background-color: var(--light-grayish-blue);
    width: 8vw;
    border-radius: 15px;
}

.plus-minus-div:hover {
    opacity: 0.5;
    cursor: pointer;
}

.add-to-cart {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--orange);
    width: 12vw;
    border-radius: 15px;
    padding: 2px 0 0 2px;
    color: white;
    box-shadow: 10px 30px 35px var(--pale-orange);
}

.add-to-cart:hover {
    cursor: pointer;
    opacity: 0.5;
}

.cart-icon {
    filter: brightness(100);
    margin-right: 45px;
}

.hover-cart {
    position: absolute;
    color: var(--dark-grayish-blue);
    background-color: white;
    top: 8vh;
    right: 15vw;
    width: 15vw;
    padding: 10px;
    border-radius: 15px;
    box-shadow: 5px 5px 15px var(--dark-grayish-blue);
    font-weight: 500;
}

.hover-cart h5 {
    color: black;
}

.hover-cart hr {
    width: auto;
}

.hover-cart-body {
    display: flex;
    flex-direction: column;
}

.hover-cart-total {
    display: flex;
    align-items: center;
    font-size: 15px;
}

.cart-total {
    color: black;
}

.cart-description p {
    margin: 5px;
}

.cart-thumbnail {
    max-height: 5vh;
    margin-right: 10px;
    border-radius: 4px;
}

.trash-icon {
    margin-left: 15px;
}

.trash-icon:hover {
    cursor: pointer;
}

.checkout-button {
    background-color: var(--orange);
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0 10px 0;
    border-radius: 10px;
    color: white;
    font-weight: 700;
}

.checkout-button:hover {
    cursor: pointer;
}

.lightbox {
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    z-index: 3;
}

.arrow {
    border-radius: 100%;
    padding: 12px 15px 12px 15px;
    position: absolute;
}

.arrow:hover {
    cursor: pointer;
    filter: invert(67%) sepia(67%) saturate(4145%) hue-rotate(346deg) brightness(100%) contrast(102%);
    background-color: rgba(255, 255, 255, 0);
}

.left-arrow {
    right: 25vw;
    background-color: var(--light-grayish-blue);
}

.right-arrow {
    left: 25vw;
    background-color: var(--light-grayish-blue);
}

.lightbox-close {
    position: absolute;
    top: 5vh;
    left: 24vw;
    height: 20px;
}

.lightbox-close:hover {
    filter: invert(67%) sepia(67%) saturate(4145%) hue-rotate(346deg) brightness(100%) contrast(102%);
}

@media (max-width: 1000px) {


    
    #main-content {
        margin-left: 30px;
        margin-right: 0;
        width: 90vw;
    }

    .desktop-nav-item {
        display: none;
    }

    .desktop-only {
        display: none;
    }

    #header {
        width: 40vw;
        margin-left: 100px;
        margin-right: 25px;
    }

    #header li {
        margin: 0 -55px 0 -55px;
    }

    .profile-pic {
        margin: 0 25px 0 125px;
        height: 30px;
    }

    #hamburger-icon {
        display: block;
        left: 110px;
        top: 27px;
        position: absolute;
    }

    .shopping-cart {
        margin-right: 10px;
    }

    #logo {
        position: absolute;
        left: 140px;
    }

    #hamburger-menu {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 70vw;
        background-color: white;
        height: 100vh;
        z-index: 3;
        margin-left: -15px;
    }

    #hamburger-menu-close {
        margin-top: 10px;
        margin-bottom: 35px;
    }

    #hamburger-menu a {
        color: black;
        font-weight: 700;
        font-size: 17px;
    }

    #hamburger-menu li {
        margin-bottom: 30px;
    }

    #main-content {
        flex-direction: column;
    }

    .product-image-small {
        display: none;
    }

    .product-image {
        margin-top: -60px;
    }

    .product-main-image {
        max-width: 100vw;
    }

    .product-image-large {
        max-width: 100vw;
    }

    .product-detail {
        max-width: 90vw;
        margin-left: 0;
        margin-top: -100px;
    }
    .prices {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .old-price {
        margin: -5px 0 0 0;
        margin-left: auto;
    }

    .add-remove-cart {
        flex-direction: column;
    }

    .add-remove-amount {
        width: 85vw;
        margin-bottom: 15px;
    }

    .add-to-cart {
        width: 85vw;
        margin-bottom: 25px;
    }

    .hover-cart {
        width: 85vw;
        height: 25vh;
        right: 10px;
    }

    .trash-icon {
        margin-left: 90px;
    }

    .checkout-button {
        height: 65px;
    }

    .product-display-desktop {
        display: none;
    }

    .product-display-mobile {
        display: block;
    }

    .mobile-left-arrow {
        position: absolute;
        bottom: 70vh;
        right: 90vw;
        background-color: var(--light-grayish-blue);
        padding: 10px 13px 10px 13px;
        border-radius: 100%;
    }

    .mobile-right-arrow {
        position: absolute;
        bottom: 70vh;
        left: 100vw;
        background-color: var(--light-grayish-blue);
        padding: 10px 13px 10px 13px;
        border-radius: 100%;
    }
}
